.footer {
  display: flex;
  justify-content:space-between;
}

.footer__nav {
  color: #585858;

  &-list {
    display: flex;
    justify-content: space-between;
  }

  &-item {
    list-style: none;
    white-space: nowrap;
    padding: 0 25px;
  }

  &-link {
    color: #585858;
    padding-bottom: 4px;
    text-decoration: none;

    &:hover {
      color: #161616;
    }
  }
}
